import _ from 'lodash'
import React, {Component} from 'react'; // create and manage the component
import ReactDOM from 'react-dom'; //interact with DOM
import YTSearch from 'youtube-api-search';

import SearchBar from './components/search_bar';
import VideoList from './components/video_list';
import VideoDetail from './components/video_detail';
import 'bootstrap/dist/css/bootstrap.min.css'
import './style/style.css';

const API_KEY = 'AIzaSyB-NuBI3BqQYLcurhVrwxqUcHUYohWa-8I';

console.log('YTSearch - ', YTSearch);
// Create a new a component. This component should produce some HTML
// const App = () => { //const means its never going to change ever and canot be changed by calling App again
//     return <div> Hello </div> //JSX is subset of javascript that allows to write HTML in JS
// }

// const App = ()=> { //this is functional component
//     return (
//         <div>
//             <SearchBar />
//         </div>
//     );
// }

class App extends Component { //this is class based component needed to record state for searching purposes
    constructor(props) {
        super(props);
        
        this.state = { 
            videos: [],
            selectedVideo: null 
        };

        this.videoSearch('iphone');
    }

    videoSearch(term){
        console.log('term - ', term);
        YTSearch({key: API_KEY, term: term}, (videos)=> {
            console.log('data - ', term);
            console.log('videos - ', videos);
            this.setState({
                videos: videos, //this is same as saying this.setState({videos:videos}) since key and value are same name
                selectedVideo: videos[0]
            }); 
        })
    }

    render() {
        const videoSearch = _.debounce(term => {this.videoSearch(term )}, 300)
 
        return (
            <div>
                <SearchBar onSearchTermChange={videoSearch} />
                <VideoDetail video={this.state.selectedVideo} />
                <VideoList 
                    onVideoSelect = {selectedVideo => this.setState({selectedVideo})}
                    videos={this.state.videos} /> {/* here videos is 'props'. passing props videos to VideoList */}
            </div>
        )
    }
}

//take this component's generated HTML and show on the page (in DOM)
ReactDOM.render(<App />, document.getElementById('root'))
// ReactDOM.render(<App />, document.querySelector('.container')); //for instance wrap in JSX tag which is <App />
                            // App by itself is JSX class
                            // here we basically render the 'App' component to the div with class 'container' 
                            //as mentioned in the main index.html file


